.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 999;
}

.menu {
  position: fixed;
  top: 0;
  right: 0;

 display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 50px;

  padding: 19px 20px 21px 20px;
  width: 55%;
  height: 100vh;
  background-color: #e6533c;

  z-index: 999;

  transform: translateX(100%);
  transition: transform 0.3s ease;

  &.active {
    transform: translateX(0);
  }

  @media screen and (min-width: 768px) {
    padding: 26px 32px 32px 32px;
    transition: transform 0.3s ease;
    width: 40%;

    &.active {
      transform: translateX(0);
    }
  }
}

.wrapNav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
padding-top: 245px;
}

.navItem {
  padding: 10px 27px;

  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: rgba(239, 237, 232, 1);

  border: 1px solid rgba(239, 237, 232, 0.2);
  border-radius: 12px;

 &:hover,
  :focus {
    border: 1px solid #EFEDE8;
  }

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}

.closeBtn {
  display: block;
  position: absolute;
  right: 18px;
  top: 19px;
 
  border: none;
  width: 24px;
  height: 24px;

  @media screen and (min-width: 375px) {
    right: 18px;
    top: 16px;
  }

  @media screen and (min-width: 768px) {
    right: 26px;
    top: 26px;
  }

  @media screen and (min-width: 1440px) {
    display: none;
  }
}

.closeIcon {
  fill: #efede8;
  width: 24px;
  height: 24px;

  @media screen and (min-width: 1440px) {
    display: none;
  }
}

.navItem {
  padding: 10px 27px;
  border: 1px solid #efede84d;
  border-radius: 12px;
  font-size: 16px;

  &:hover {
    border-color: #e6533c;
  }

  &:active {
    border-color: #e6533c;
    background-color: #e6533c;
  }
}

@media (max-width: 768px) {
  .navItem {
    border: 1px solid #efede84d;
  }
}

.btnLogout {
  text-align: left;
}

.mobileBtn {
  position: absolute;
  top: 26px;
  right: 32px;
  background: none;
  border: none;
